// ** React Imports
import { useState, useEffect } from 'react'
import Avatar from '@src/@core/components/avatar'
// ** Reactstrap Imports
import {
  Card,
  CardTitle,
  CardHeader,
  CardBody,
  Col,
  ListGroupItem,
  Row
} from 'reactstrap'

// ** Styles
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useParams } from 'react-router-dom'
import axios from '@src/configs/axios/axiosConfig'
import moment from 'moment'

const Documents = ({ tabId }) => {
  // ** Store Vars
  const { id } = useParams()
  const [attachmentList, setAttachmentList] = useState([])
  const fileClassNames = {
    image: "ui-attachment--image",
    pdf: "ui-attachment--pdf",
    xlsx: "ui-attachment--excel",
    xls: "ui-attachment--excel",
    docx: "ui-attachment--docx",
    doc: "ui-attachment--docx"
  }

  const getData = () => {

    axios.post('/tasks/attachmentidslistbytaskid', { taskId: id })
      .then((res) => {
        setAttachmentList(res.data.attachment_lists)
      })
      .catch((err) => { console.log(err) })

  }

  useEffect(() => {
    if (tabId === 'taskdocuments') {
      getData()
    }

  }, [tabId])

  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <CardHeader className='py-1'>
          <CardTitle tag='h4'>Attachments</CardTitle>
        </CardHeader>
        <CardBody>
          {
            attachmentList.map((attachment, j) => {

              return (

                <Row key={j} className='px-1'>
                  <Row className='px-0 py-50 d-flex justify-content-between'>
                    <Col>
                      <Avatar initials color={'light-primary'} content={attachment.username?.charAt(0)} className='text-primary primary me-50' />
                      {attachment.username}</Col>
                    <Col className='d-flex justify-content-between'>
                      <span></span>
                      {moment(attachment.createdon, 'x').format('MMM DD, Y')}
                    </Col>
                  </Row>

                  {
                    attachment.files.map((file, k) => {
                      return (
                        <Col md={'2'} key={k} className='ps-3 file-details d-flex align-items-center cursor-pointer'>
                          <ListGroupItem className={`col-12 d-flex mb-1 align-items-center ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
                            <a target='_blank' className='file-name text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 15 ? `${file.originalfilename.split('.')[0].substr(0, 15)}.${file.extension}` : file.originalfilename}</a>
                          </ListGroupItem>
                        </Col>
                      )
                    })
                  }
                </Row>
              )
            })
          }
        </CardBody>
      </Card>
    </div>
  )
}

export default Documents
