// ** React Imports
import { Fragment, useEffect, useState } from 'react'
// ** Reactstrap Imports
import {
  Card,
  CardBody,
  Col,
  Row,
  ListGroupItem
} from 'reactstrap'

// ** Third Party Components
import 'cleave.js/dist/addons/cleave-phone.us'
import Avatar from '@src/@core/components/avatar'
import { getServiceTemplates } from '@src/views/apps/service/store/templates'
import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment'

const Templates = props => {
  // ** Hook
  const { tabId, selectedTask } = props

  const renderClient = row => {

    return (
      <Avatar
        initials
        className='me-1'
        color={'light-primary'}
        content={row.username?.charAt(0) || 'T'}
      />
    )
  }

  const fileClassNames = {
    image: "ui-attachment--image",
    pdf: "ui-attachment--pdf",
    xlsx: "ui-attachment--excel",
    xls: "ui-attachment--excel",
    docx: "ui-attachment--docx",
    doc: "ui-attachment--docx"
  }

  const dispatch = useDispatch()
  //** State */
  const [data, setData] = useState([])
  const store = useSelector(state => state.templates)

  const dateformat = (val) => {

    return moment(val, 'x').format('YYYY, MMM-D h:m A')

  }

  useEffect(async () => {
    if (tabId === 'taskservicetemplate') {
      await dispatch(getServiceTemplates(selectedTask.serviceid))
    }
  }, [tabId])

  useEffect(() => {
    setData(store.data || [])
  }, [store.data])

  return (
    <Fragment>
      <Card>
        <CardBody>

          {data.map((item, k) => {
            return (
              <div className='d-flex align-items-start my-1' key={k}>
                {renderClient(item)}
                <div className='profile-user-info w-100'>
                  <div className='d-flex align-items-center justify-content-between'>
                    <h6 className='mb-0'>{item.username}</h6>
                  </div>
                  <Row className='mt-1'>
                    <div className='white-space-wrap'>{item.description.replace(/(\\n)/g, "\n")}</div>
                    <Row>
                      {
                        item.files[0] !== null &&
                        item.files.map((file, k) => {
                          return (
                            <Col md={'2'} key={k} className={`file-details d-flex align-items-center cursor-pointer`}>
                              <ListGroupItem className={`col-12 d-flex mb-1 align-items-center ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
                                <a target='_blank' className='file-name mb- text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 15 ? `${file.originalfilename.split('.')[0].substr(0, 15)}.${file.extension}` : file.originalfilename}</a>
                              </ListGroupItem>
                            </Col>
                          )
                        })
                      }
                    </Row>
                    <Row>
                      <div className='d-flex mt-1'>
                        <span className='text-sm'>{dateformat(item.createdon)}</span>
                      </div>
                    </Row>
                  </Row>
                </div>
              </div>
            )
          })
          }
        </CardBody>
      </Card>
    </Fragment>
  )
}

export default Templates
