// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import toast from 'react-hot-toast'
import { useDropzone } from 'react-dropzone'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
  Card,
  Input,
  Button,
  CardBody,
  Label,
  ListGroup,
  ListGroupItem,
  Col,
  Row,
  Progress,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'

// ** Third Party Components
import 'cleave.js/dist/addons/cleave-phone.us'
import { X, Paperclip, File, MoreVertical, Edit2, Trash, CornerUpRight } from 'react-feather'
import Avatar from '@src/@core/components/avatar'
import { getTaskConversation, addTaskConversation, deleteTaskConversation, updateTaskConversation } from '../store/taskconversation'
import { useDispatch, useSelector } from 'react-redux'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
import { useParams } from 'react-router-dom'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const MySwal = withReactContent(Swal)
// ** Images
const Templates = props => {
  // ** Hook
  const { tabId } = props

  const renderClient = row => {

    return (
      <Avatar
        initials
        className='me-1'
        color={'light-primary'}
        content={row?.username?.charAt(0) || 'T'}
      />
    )

  }
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const dispatch = useDispatch()
  //** State */
  const { id } = useParams()
  const [filesArr, setFilesArr] = useState([])
  //const [files, setFiles] = useState([])
  const [data, setData] = useState([])
  const [attachmentIds, setAttachmentsId] = useState([])
  const [conversationId, setConversationId] = useState('')
  const [comment, setComment] = useState('')
  const [replycomment, setReplyComment] = useState('')
  const [editcomment, setEditComment] = useState('')
  const [fileUploadProgress, setProgress] = useState(0)

  const store = useSelector(state => state.taskconversation)
  const fileClassNames = {
    image: "ui-attachment--image",
    pdf: "ui-attachment--pdf",
    xlsx: "ui-attachment--excel",
    xls: "ui-attachment--excel",
    docx: "ui-attachment--docx",
    doc: "ui-attachment--docx"
  }
  const deletefn = (conid) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteTaskConversation({ id: conid, taskId: id }))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Comments has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const editNote = (id, step = 1) => {
    const temp = data.map((obj) => {
      let loc = obj.reply
      if (step === 2) {
        loc = obj?.reply?.map((oj) => {
          if (oj.id === id) {
            setEditComment(oj.comment)
            return { ...oj, editFlag: true }
          } else {
            return { ...oj, editFlag: false }
          }
        })
      }

      if (obj.id === id) {
        setEditComment(obj.comment)
        return { ...obj, editFlag: true, reply: loc }
      } else {
        return { ...obj, editFlag: false, reply: loc }
      }

    })
    console.log(temp)
    setData(temp)
  }

  const dateformat = (val) => {

    return moment(val, 'x').format('YYYY, MMM-D h:m A')

  }

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      //Set the progress value to show the progress bar
      setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const uploadFiles = (files) => {

    setBlock(true)
    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })
    formData.append('id', id)
    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', userId)

    axios.post('/google/taskfolders', formData, configUpload).then((res) => {
      setAttachmentsId([...attachmentIds, ...res.data.attachment_ids])
      //setFiles([])
      setBlock(false)
    }).catch(() => {
      toast.error('Upload Failed', { position: 'bottom-right' })
      setBlock(false)
    })
  }

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: async (files, rejectedFiles) => {

      const acceptedFiles = []
      files.forEach(obj => {
        obj.extension = obj.name.split('.').pop()
        acceptedFiles.push(obj)
      })
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        await uploadFiles(acceptedFiles)
        //setFiles(acceptedFiles)
        setFilesArr([...filesArr, ...acceptedFiles])
      }
    }
  })

  const handleRemoveFile = (file, index) => {
    const uploadedFiles = filesArr
    const filtered = uploadedFiles.filter(i => i.name !== file.name)
    setAttachmentsId([...attachmentIds.splice(index, 1)])
    //setFiles(filtered)
    setFilesArr(filtered)
  }

  const saveNote = async (obj) => {
    obj.comment = editcomment
    if (obj.comment !== '') {
      obj['updatedBy'] = userId
      await dispatch(updateTaskConversation(obj))
      toast.success('Comment Updated Successfully', { position: 'bottom-right' })
    }
  }

  useEffect(async () => {
    if (tabId === 'tasknotes') {
      await dispatch(getTaskConversation(id))
    }
  }, [tabId])

  useEffect(() => {
    setData(store.data || [])
  }, [store.data])

  const postComment = async () => {
    if ((comment !== '' || replycomment !== '') || attachmentIds.length > 0) {
      const data = {
        organizationId: activeOrgId,
        createdBy: userId,
        updatedBy: userId,
        comment: (conversationId === null || conversationId === '') ? comment : replycomment,
        attachmentIds,
        conversationId,
        taskId: id
      }
      setBlock(true)
      await dispatch(addTaskConversation(data))
      toast.success('Comment Added Successfully', { position: 'bottom-right' })
      setComment('')
      //setFiles([])
      setFilesArr([])
      setProgress(0)
      setAttachmentsId([])
      setBlock(false)
    }

  }

  const fileList = filesArr.map((file, index) => (
    <Col md={2} key={`${file.name}-${index}`} className={`d-flex align-items-center justify-content-between ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
      <div className='file-details d-flex align-items-center'>
        <div>
          <p className='file-name mb-0 text-overflow-ellipsis'>{file.name.split('.')[0].length > 15 ? `${file.name.split('.')[0].substr(0, 15)}` : file.name}</p>
        </div>
      </div>

      <X className='cursor-pointer' size={14} onClick={() => handleRemoveFile(file, index)} />

    </Col>
  ))

  const replyNote = (k) => {
    const temp = data.map((obj, i) => {
      if (i === k) {
        setConversationId(obj.id)
        return { ...obj, replyFlag: true }
      } else {
        return { ...obj, replyFlag: false }
      }
    })
    setData(temp)
  }

  const cancelReply = () => {
    setConversationId(null)
    setReplyComment('')
    const temp = data.map((obj) => {
      return { ...obj, replyFlag: false }
    })
    setData(temp)
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardBody>

            <fieldset className='form-label-group mb-50'>
              <Label className='form-check-label' for={`add-comment-`}>
                Add Comment
              </Label>
              <Input id={`add-comment-`} type='textarea' rows='3' placeholder='Add Comment' value={comment} onInput={(e) => setComment(e.target.value)} />
            </fieldset>

            <input {...getInputProps()} />

            <Button type='button' color='primary' size='sm' {...getRootProps({ })} >
              <Paperclip size={14} className=''></Paperclip>
            </Button>

            <Button color='primary' className='ms-1' size='sm' onClick={() => postComment()}>
              Post Comment
            </Button>

            {fileUploadProgress > 0 && <Progress className='mt-md-50' color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}

            <Row className='d-flex p-1'>{fileList}</Row>

            {data.map((item, k) => {
              return (
                <div className='d-flex align-items-start my-1' key={k}>
                  {renderClient(item)}
                  <div className='profile-user-info w-100'>
                    <div className='d-flex align-items-center justify-content-between'>
                      <h6 className='mb-0'>{item.username}</h6>
                      <span className='d-flex text-body'>
                        <span className='text-sm text-body font-small-3 mt-25'>{dateformat(item.createdon)}</span>
                        {(!item.editFlag && !item.replyFlag) &&
                          <UncontrolledDropdown>
                            <DropdownToggle tag='span'>
                              <MoreVertical size={16} />
                            </DropdownToggle>
                            <DropdownMenu end>
                              <DropdownItem>
                                <Col tag={'a'} href='/' onClick={(e) => { e.preventDefault(); editNote(item.id, 1) }}>
                                  <Edit2 size={13} /> Edit
                                </Col>
                              </DropdownItem>
                              <DropdownItem>
                                <Col tag={'a'} href='/' onClick={(e) => { e.preventDefault(); deletefn(item.id) }}>
                                  <Trash size={13} /> Delete
                                </Col>
                              </DropdownItem>
                              <DropdownItem>
                                <Col tag={'a'} href='/' onClick={(e) => { e.preventDefault(); replyNote(k) }}>
                                  <CornerUpRight size={13} /> Reply
                                </Col>
                              </DropdownItem>
                            </DropdownMenu>
                          </UncontrolledDropdown>
                        }
                      </span>
                    </div>
                    {(!item.editFlag) &&
                      <Row className='mt-1'>

                        <div className='white-space-wrap text-body '>
                          {item.comment !== '' &&
                            < span className='rounded-3 bg-light-primary chat-bubble'>
                              {item.comment.replace(/(\\n)/g, "\n")}
                            </span>
                          }
                          <Row>
                            {
                              item?.files?.map((file, k) => {
                                return (
                                  <Col md={'2'} key={k} className='file-details d-flex align-items-center cursor-pointer'>
                                    <ListGroupItem className={`col-12 d-flex mb-1 align-items-center ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
                                      <a target='_blank' className='file-name text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 15 ? `${file.originalfilename.split('.')[0].substr(0, 15)}.${file.extension}` : file.originalfilename}</a>
                                    </ListGroupItem>
                                  </Col>
                                )
                              })
                            }
                          </Row>
                        </div>
                      </Row>
                    }
                    {item.editFlag &&
                      <Col className='mt-2'>
                        <Input type='textarea' rows='3' value={editcomment.replace(/(\\n)/g, "\n")} onInput={(e) => { setEditComment(e.target.value) }} />

                        <Col className='d-flex justify-content-end mt-1' >
                          <Button size='sm' color='warning' outline onClick={() => editNote(-1, 1)}>Cancel</Button>
                          <Button size='sm' color='primary' className='ms-1' onClick={() => saveNote(item)} >Save</Button>
                        </Col>
                      </Col>
                    }
                    {item.replyFlag &&
                      <Col>
                        <fieldset className='form-label-group mb-50'>
                          <Label className='form-check-label' for={`add-comment-`}>
                            Add Comment
                          </Label>
                          <Input id={`add-comment-`} type='textarea' rows='3' placeholder='Add Comment' value={replycomment} onInput={(e) => setReplyComment(e.target.value)} />
                        </fieldset>
                        <input {...getInputProps()} />

                        <Button type='button' color='primary' size='sm' {...getRootProps({  })} >
                          <Paperclip size={14} className=''></Paperclip>
                        </Button>

                        <Button color='primary' className='ms-1' size='sm' onClick={() => postComment()}>
                          Send Reply
                        </Button>
                        <Button color='warning' className='ms-1' size='sm' onClick={() => cancelReply()}>
                          Cancel
                        </Button>

                        {fileUploadProgress > 0 && <Progress className='mt-md-50' color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}

                        <Row className='d-flex p-1'>{fileList}</Row>
                      </Col>
                    }

                    {
                      item?.reply?.map((reply, j) => {
                        return (
                          reply !== null &&
                          <div className='d-flex align-items-start my-1' key={j}>
                            {renderClient(reply)}
                            <div className='profile-user-info w-100'>
                              <div className='d-flex align-items-center justify-content-between'>
                                <h6 className='mb-0'>{reply.username}</h6>
                                <span className='d-flex text-body'>
                                  <span className='text-sm text-body font-small-3 mt-25'>{dateformat(reply.createdon)}</span>
                                  {(!item.editFlag && !item.replyFlag) &&
                                    <UncontrolledDropdown>
                                      <DropdownToggle tag='span'>
                                        <MoreVertical size={16} />
                                      </DropdownToggle>
                                      <DropdownMenu end>
                                        <DropdownItem>
                                          <Col tag={'a'} href='/' onClick={(e) => { e.preventDefault(); editNote(reply.id, 2) }}>
                                            <Edit2 size={13} /> Edit
                                          </Col>
                                        </DropdownItem>
                                        <DropdownItem>
                                          <Col tag={'a'} href='/' onClick={(e) => { e.preventDefault(); deletefn(reply.id) }}>
                                            <Trash size={13} /> Delete
                                          </Col>
                                        </DropdownItem>
                                      </DropdownMenu>
                                    </UncontrolledDropdown>
                                  }
                                </span>
                              </div>
                              <Row className='mt-1'>
                                {!reply?.editFlag &&
                                  <div className='white-space-wrap text-body '>
                                    {reply.comment !== '' &&
                                      < span className='rounded-3 bg-light-warning chat-bubble-reply'>
                                        {reply.comment.replace(/(\\n)/g, "\n")}
                                      </span>
                                    }
                                    <Row>
                                      {
                                        reply?.files?.map((file, k) => {
                                          return (
                                            <Col md={'2'} key={k} className='file-details d-flex align-items-center cursor-pointer'>
                                              <ListGroupItem className={`col-12 d-flex mb-1 align-items-center ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
                                                <a target='_blank' className='file-name text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 15 ? `${file.originalfilename.split('.')[0].substr(0, 15)}.${file.extension}` : file.originalfilename}</a>
                                              </ListGroupItem>
                                            </Col>
                                          )
                                        })
                                      }
                                    </Row>
                                  </div>
                                }
                                {reply?.editFlag &&
                                  <Col className='mt-2'>
                                    <Input type='textarea' rows='3' value={editcomment.replace(/(\\n)/g, "\n")} onInput={(e) => { setEditComment(e.target.value) }} />

                                    <Col className='d-flex justify-content-end mt-1' >
                                      <Button size='sm' color='warning' outline onClick={() => editNote(-1, 2)}>Cancel</Button>
                                      <Button size='sm' color='primary' className='ms-1' onClick={() => saveNote(reply)} >Save</Button>
                                    </Col>
                                  </Col>
                                }
                              </Row>
                            </div>
                          </div>
                        )
                      })
                    }
                  </div>
                </div>
              )
            })
            }
          </CardBody>
        </Card>
      </UILoader>
    </Fragment >
  )
}

export default Templates
