import { useEffect, useState, Fragment, useRef } from 'react'
import { useDispatch } from 'react-redux'
import { useParams } from 'react-router-dom'
import { checklists } from '../store/index'
// ** Reactstrap Imports
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import { Card, CardBody, UncontrolledDropdown, DropdownToggle, DropdownItem, DropdownMenu, Row, Input, Button, Col, Progress, ListGroupItem, Badge, Modal, ModalHeader, ModalBody } from 'reactstrap'
import { CheckCircle, MoreVertical, RefreshCcw, Paperclip, X, Plus } from 'react-feather'
import axios from '@src/configs/axios/axiosConfig'
import { orgUserId, activeOrganizationid, activeOrganization } from '@src/helper/sassHelper'
import { useDropzone } from 'react-dropzone'
import moment from 'moment'
const userId = orgUserId()
const activeOrg = activeOrganization()
const activeOrgId = activeOrganizationid()
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { Editor } from 'react-draft-wysiwyg'
import draftToHtml from 'draftjs-to-html'
import { convertToRaw } from 'draft-js'
import '@styles/react/libs/editor/editor.scss'
import { ReactSortable } from 'react-sortablejs'
// ** Timeline 
const fileClassNames = {
  image: "ui-attachment--image",
  pdf: "ui-attachment--pdf",
  xlsx: "ui-attachment--excel",
  xls: "ui-attachment--excel",
  docx: "ui-attachment--docx",
  doc: "ui-attachment--docx"
}
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import toast from 'react-hot-toast'
const MySwal = withReactContent(Swal)
const CommentBox = ({ taskId, workflowId, handlchecklist, workflow }) => {

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const { id } = useParams()
  const domInputRef = useRef(null)
  const [description, setDescription] = useState('')
  const [filesArr, setFilesArr] = useState([])
  //const [files, setFiles] = useState([])
  const [data, setData] = useState('')
  const [attachmentIds, setAttachmentsId] = useState([])
  const [fileUploadProgress, setProgress] = useState(0)
  const [workflowname, setWorkflowname] = useState('')


  useEffect(() => {
    setWorkflowname(workflow?.title)
  }, [workflow])

  const submitComment = () => {
    if (description === '' && attachmentIds.length === 0) {
      return false
    }
    const obj = {
      workflowId,
      taskId,
      description,
      createdBy: userId,
      organizationId: activeOrgId,
      attachmentIds
    }
    setData('')
    setBlock(true)
    axios.post('/comments/add', obj)
      .then(() => {
        //setFiles([])
        setFilesArr([])
        setProgress(0)
        setAttachmentsId([])
        setBlock(false)
        handlchecklist(true)
        if (domInputRef.current) {
          domInputRef.current.innerHTML = ''
        }
      })
      .catch((err) => { console.log(err); setBlock(false) })

  }

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      //Set the progress value to show the progress bar
      setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const uploadFiles = (files) => {

    setBlock(true)
    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })
    formData.append('id', id)
    formData.append('workflowname', workflowname)
    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', userId)

    axios.post('/google/taskworkflowfolders', formData, configUpload).then((res) => {
      setAttachmentsId([...attachmentIds, ...res.data.attachment_ids])
      //setFiles([])
      setBlock(false)
    }).catch(() => {
      toast.error('Upload Failed', { position: 'bottom-right' })
      setBlock(false)
    })
  }

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: async (files, rejectedFiles) => {

      const acceptedFiles = []
      files.forEach(obj => {
        obj.extension = obj.name.split('.').pop()
        acceptedFiles.push(obj)
      })
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        await uploadFiles(acceptedFiles)
        //setFiles(acceptedFiles)
        setFilesArr([...filesArr, ...acceptedFiles])
      }
    }
  })

  const handleContentEditable = (val) => {
    const des = draftToHtml(convertToRaw(val.getCurrentContent()))
    setDescription(des)

    setData(val)
  }

  const handleRemoveFile = (file, index) => {
    const uploadedFiles = filesArr
    const filtered = uploadedFiles.filter(i => i.name !== file.name)
    setAttachmentsId([...attachmentIds.splice(index, 1)])
    //setFiles(filtered)
    setFilesArr(filtered)
  }

  const RenderFile = ({ filesArr }) => {

    return (
      <Row>
        <Row className='d-flex p-1'>
          {
            filesArr.map((file, index) => {
              return (<Col md={2} key={`${file.name}-${index}`} className={`d-flex align-items-center justify-content-between ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
                <div className='file-details d-flex align-items-center'>
                  <div>
                    <p className='file-name mb-0 text-overflow-ellipsis'>{file.name.split('.')[0].length > 15 ? `${file.name.split('.')[0].substr(0, 15)}` : file.name}</p>
                  </div>
                </div>
                <X size={14} className='cursor-pointer' onClick={() => handleRemoveFile(file, index)} />
              </Col>
              )
            })
          }
        </Row>
      </Row>
    )
  }

  return (
    <UILoader blocking={block} loader={<Loader />}>
      <Row className='pe-2'>
        {/* <div contentEditable
          className='border border-1 p-50 rounded-2'
          ref={domInputRef}
          onInput={(e) => { handleContentEditable(e.currentTarget.textContent) }} >

        </div> */}
        <Editor
          placeholder='Message'
          toolbarClassName='rounded-0'
          wrapperClassName='toolbar-bottom mt-50'
          editorState={data}
          onEditorStateChange={handleContentEditable}
          editorClassName='rounded-0 border-1'
        />
        <Col className='mt-md-50 ps-1'>
          <input {...getInputProps()} />
          <Button type='button' color='primary' size='sm' {...getRootProps({ className: 'dropzone me-1' })} >
            <Paperclip size={14} className=''></Paperclip>
          </Button>
          <Button size='sm' color='primary' onClick={() => { submitComment() }}>Submit</Button>
        </Col>
        {fileUploadProgress > 0 && <Progress className='mt-md-50' color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}
        <RenderFile filesArr={filesArr} fileUploadProgress={fileUploadProgress} />
      </Row>
    </UILoader>
  )
}

const CheckList = ({ tabId, switchSubTaskView }) => {
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const [formModal, setFormModal] = useState(false)
  //const [files, setFiles] = useState([])
  const [userOptions, setuserOptions] = useState([])
  const { id } = useParams()
  const dispatch = useDispatch()
  const [data, setData] = useState([])
  const [selData, setSelData] = useState(null)
  const [userids, setUserIds] = useState([])
  const colourStyles = {
    multiValue: (styles, { }) => {

      return {
        ...styles,
        backgroundColor: '#7367f0'
      }
    }
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data.organizationusers
      setuserOptions(arr)
    }).catch((err) => {
      console.log(err)
    })
  }

  const getChecklists = async () => {
    setBlock(true)
    const res = await dispatch(checklists({ taskId: id }))
    await setData(res.payload)
    setBlock(false)
  }

  const tagUser = () => {
    axios.post('/taskworkflows/assignuser', { userids: userids.map((oj) => oj.id), id: selData.id, updatedBy: userId })
      .then(() => {
        getChecklists()
        setFormModal(false)
        setUserIds([])
      })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  const deleteUser = (userList, userid, workflowid) => {
    const temp = userList.map((oj) => oj.id)
    temp.splice(temp.indexOf(userid), 1)
    axios.post('/taskworkflows/assignuser', { userids: temp, id: workflowid, updatedBy: userId })
      .then(() => {
        getChecklists()
        setFormModal(false)
        setUserIds([])
      })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  const removeuser = (userList, userid, workflowid) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {

        await deleteUser(userList, userid, workflowid)
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Sub Task user has been removed.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })

  }

  const assignUserModal = (val) => {
    setSelData(val)
    setFormModal(true)
  }

  const dropdownStyle = {
    position: 'absolute',
    left: '-34px',
    top: '-4px'
  }
  const inputCheckBoxStyle = {
    position: 'absolute',
    left: '-53px',
    top: '-4px'
  }
  useEffect(() => {
    if (tabId === 'checklist') {
      getChecklists()
      getOrganizationUsers()
    }
  }, [tabId])

  const updateStatus = (sts, id) => {
    setBlock(true)
    axios.post('/taskworkflows/updatestatus', { status: sts, id, updatedBy: userId })
      .then(() => { getChecklists() })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }
  }

  const dateFormat1 = (value) => {

    return moment(value, 'x').format("MMM DD, YYYY")

  }

  const handlchecklist = (val) => {
    if (val) {
      getChecklists()
    }
  }

  const updatePositionfn = () => {
    setBlock(true)
    axios.post('/taskworkflows/updateposition', { workflows: data })
      .then(() => {
        toast.success('Checklist sorted Successfully', { position: 'bottom-right' })
        setBlock(false)
        setSortable(true)
      })
      .catch(() => { })
  }

  const renderComments = (obj) => {
    if (obj !== null) {
      return (
        <div className={`p-md-50 p-50 p-lg-50 rounded-2 bg-light-primary mt-md-50`} key={obj.id}>
          {
            <div className='d-flex justify-content-between'>
              <h6>{obj.name}</h6>
              <h6>{dateFormat1(obj.createdon)}</h6>
            </div>
          }
          <p className='white-space-wrap' dangerouslySetInnerHTML={{ __html: obj.comments }} ></p>
          <Row>
            {
              obj.files[0] !== null &&
              obj.files.map((file, k) => {
                return (
                  <Col md={'3'} key={k} className='file-details d-flex align-items-center cursor-pointer'>
                    <ListGroupItem className={`col-12 d-flex mb-1 align-items-center ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
                      <a target='_blank' className='file-name text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 15 ? `${file.originalfilename.split('.')[0].substr(0, 15)}.${file.extension}` : file.originalfilename}</a>
                    </ListGroupItem>
                  </Col>
                )
              })
            }
          </Row>
        </div>
      )
    }
  }

  const exportPdf = () => {
    axios.post('/taskworkflows/exportpdf', { taskId: id })
      .then((res) => {
        const downloadLink = document.createElement("a")
        const fileName = `checklist.pdf`
        downloadLink.href = res.data.pdf
        downloadLink.download = fileName
        downloadLink.click()
        document.removeChild(downloadLink)
      })
      .catch((err) => { console.log(err) })
  }
  const [selctedIds, setSelctedIds] = useState([])
  const statusBulkUpdate = (sts) => {

    setBlock(true)
    axios.post('/taskworkflows/updatestatusbulk', { status: sts, selctedIds, updatedBy: userId })
      .then(() => { getChecklists(); setSelctedIds([]) })
      .catch((err) => { console.log(err); setBlock(false) })

  }

  const handleCheckBox = (e, id) => {
    if (e.target.checked) {
      setSelctedIds([...selctedIds, id])
    } else {
      const temp = [...selctedIds]
      temp.pop(id)
      setSelctedIds(temp)
    }
  }

  return (
    <Card>
      <UILoader blocking={block} loader={<Loader />}>
        <CardBody className='pt-1'>
          <Col className='p-2 d-flex justify-content-between align-items-center'>
            Sub Task
            <Col>
              <UncontrolledDropdown className='ms-1'>
                <DropdownToggle color='secondary' caret outline>
                  Detailed View
                </DropdownToggle>
                <DropdownMenu>
                  <DropdownItem onClick={() => { switchSubTaskView(true) }} className='w-100'>
                    <span className='align-middle'>List View </span>
                  </DropdownItem>
                  <DropdownItem onClick={() => { switchSubTaskView(false) }} className='w-100'>
                    <span className='align-middle'>Detailed View</span>
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Col>
            <Col className='d-flex col-md-4 align-items-center'>
              <Col className='d-flex'>
                {
                  selctedIds.length > 0 &&
                  <Fragment>
                    <Button color='success' outline size='sm' className='me-1' onClick={() => { statusBulkUpdate(3) }}>Mark as Complete</Button>
                  </Fragment>
                }
              </Col>
              <Button outline color='primary' onClick={() => { exportPdf() }}>Export</Button>
            </Col>
          </Col>

          <Col className='p-1'>
            <ul className="timeline ms-3">
              <ReactSortable className='row sortable-row' list={data} setList={setData} onSort={() => { updatePositionfn() }}>
                {data && data.map((item, ind) => {
                  return (

                    <li className="timeline-item" key={ind}>

                      {item.progressstatus !== undefined &&
                        <Fragment>

                          {item.progressstatus === '1' &&
                            <Input type='checkbox' className='me-4' style={inputCheckBoxStyle} onChange={(e) => { handleCheckBox(e, item.id) }} />
                          }
                          <UncontrolledDropdown style={dropdownStyle}>
                            <DropdownToggle tag='div' className='btn btn-sm p-25'>
                              <MoreVertical size={14} className='cursor-pointer' />
                            </DropdownToggle>
                            <DropdownMenu>
                              {item.progressstatus === '1' &&
                                <DropdownItem onClick={() => updateStatus(3, item.id)}>
                                  <CheckCircle size={14} /> Mark as Complete
                                </DropdownItem>
                              }
                              {item.progressstatus === '3' &&
                                <DropdownItem onClick={() => updateStatus(1, item.id)}>
                                  <RefreshCcw size={14} /> Reopen
                                </DropdownItem>
                              }
                            </DropdownMenu>
                          </UncontrolledDropdown>
                        </Fragment>
                      }
                      <span className={`timeline-point timeline-point-indicator ${(item.progressstatus === '3' || item.progressstatus === undefined) ? 'timeline-point-success' : ''}`}></span>
                      <div className="timeline-event">
                        <div className="d-flex flex-sm-row flex-column mb-50">
                          <h6>{item.title}</h6>
                          {
                            (activeOrg.rolelevel === 1 || activeOrg.rolelevel === 2) &&
                            <Button size='sm' className='ms-1' color='primary' onClick={() => { assignUserModal(item) }}> <Plus size='14' /> Assign User</Button>
                          }
                        </div>
                        {item.comments !== '' &&
                          <div className={`p-md-50 p-50 p-lg-50 rounded-2 bg-light-${(item.progressstatus === '3' || item.progressstatus === undefined) ? 'success' : 'primary'}`}>
                            {
                              item.progressstatus === undefined &&
                              (
                                <div className='d-flex justify-content-between'>
                                  <h6>{item.name}</h6>
                                  <h6>{dateFormat(item.createdon)}</h6>
                                </div>
                              )
                            }

                            <p className='white-space-wrap' dangerouslySetInnerHTML={{ __html: item.comments.replace('\\', '') }} ></p>

                          </div>
                        }
                        {
                          item?.users?.map((obj, k) => {
                            return (
                              <Badge color='primary' className='me-50 mt-50' key={k} onClick={() => { removeuser(item.users, obj, item.id) }}>{obj.name}
                                <X size={14} className='ms-50' />
                              </Badge>
                            )
                          })
                        }
                        {
                          item.progressstatus === '1' &&
                          (
                            <CommentBox taskId={id} workflowId={item.id} handlchecklist={handlchecklist} workflow={item} />

                          )
                        }
                        {
                          (item.progressstatus !== undefined && item.taskcomments !== undefined) &&
                          (
                            item.taskcomments.map((obj) => {
                              return renderComments(obj)
                            })
                          )
                        }
                      </div>
                    </li>

                  )
                })
                }
              </ReactSortable>
            </ul>
          </Col>
          <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
            <UILoader blocking={block} loader={<Loader />}>
              <ModalHeader toggle={() => setFormModal(!formModal)}>Bulk Task Re Assign</ModalHeader>
              <ModalBody>
                <Row>
                  <Col>
                    <Select
                      isClearable={false}
                      value={userids}
                      options={userOptions}
                      className='react-select'
                      classNamePrefix='select'
                      isMulti
                      theme={selectThemeColors}
                      getOptionLabel={(option) => option.name}
                      getOptionValue={(option) => option.id}
                      styles={colourStyles}
                      onChange={async data => {
                        setUserIds(data)
                      }}
                    />
                  </Col>
                </Row>

                <Row className='d-flex justify-content-between mt-1'>
                  <Col>
                  </Col>
                  <Col>
                    <Button color='warning' onClick={() => { setFormModal(false) }}>Cancel</Button>
                    <Button color='primary' className='ms-1' onClick={() => { tagUser() }}>Submit</Button>
                  </Col>
                </Row>
              </ModalBody>
            </UILoader>
          </Modal>
        </CardBody>
      </UILoader>
    </Card>
  )
}

export default CheckList
