// ** Reactstrap Imports
// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import Flatpickr from 'react-flatpickr'
import Select from 'react-select'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Form,
  Label,
  Input,
  Button,
  CardBody,
  FormFeedback,
  Table,
  Modal,
  CardTitle,
  ModalHeader,
  Badge,
  ModalBody,
  Progress
} from 'reactstrap'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import axios from '@src/configs/axios/axiosConfig'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Third Party Components
import classnames from 'classnames'
import moment from 'moment'
import toast from 'react-hot-toast'
import { Edit, Trash2, Plus, X } from 'react-feather'
import { Link, useParams } from 'react-router-dom'
import { useDropzone } from 'react-dropzone'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const Forms = ({ tabId, selTask }) => {

  const MySwal = withReactContent(Swal)
  const { id } = useParams()
  const [taskFormId, setTaskFormId] = useState(null)
  const [formModal, setFormModal] = useState(false)
  const [selected, setSelected] = useState(null)
  const [filesArr, setFilesArr] = useState([])
  const [formsList, setFormsList] = useState([])
  const [attachmentIds, setAttachmentsId] = useState([])
  const [fileUploadProgress, setProgress] = useState(0)
  const [formName, setFormName] = useState('')
  const statusArr = ['', 'Not Filed', 'Filed']
  const statusColArr = ['', 'warning', 'success']
  const fileClassNames = {
    image: "ui-attachment--image",
    pdf: "ui-attachment--pdf",
    xlsx: "ui-attachment--excel",
    xls: "ui-attachment--excel",
    docx: "ui-attachment--docx",
    doc: "ui-attachment--docx"
  }
  const schema = yup.object().shape({
    id: yup.string().nullable(),
    taskId: yup.string().default(id),
    status: yup.number().default(1),
    createdby: yup.string().default(userId),
    updatedby: yup.string().default(userId),
    remarks: yup.string().default(''),
    organizationid: yup.string().default(activeOrgId),
    formid: yup.string(),
    fileddate: yup.string().default('').nullable()
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const resetForm = () => {
    setValue('formid', '')
    setValue('fileddate', '')
    setValue('attachmentids', '')
    setValue('taskId', id)
    setValue('id', '')
    setValue('attachmentIds', [])
    setValue('organizationid', activeOrgId)
    setValue('createdby', userId)
  }

  const closeModal = () => {
    setFormModal(false)
    resetForm()
  }

  const getFormsList = () => {
    axios.post('/taskforms/list', { taskId: selTask.id })
      .then((res) => {
        setFormsList(res.data.taskforms)
      })
  }

  const deletefn = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await axios.post('/taskforms/delete', { id, updatedBy: userId })
        getFormsList()
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'License has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const openEditModal = card => {

    let form = card
    let flg = false
    if (formsList.length > 0) {
      form = formsList.find((obj) => card.id === obj.formid)
      if (form) {
        flg = true
      } else {
        flg = false
      }
    }

    form = form ?? card
    const issueddate = (flg && form?.fileddate !== '0') ? moment(form?.fileddate, 'x').format('MMM DD, Y') : null
    setSelected(form)
    setFormModal(true)
    setFormName(card?.name)
    setTaskFormId(flg ? form.id : null)
    setValue('id', flg ? form.id : null)
    setValue('formid', flg ? form.formid : form.id)
    setValue('fileddate', issueddate)
    setValue('status', parseInt(form?.status))
    setValue('taskId', selTask.id)
    setValue('attachments', form?.attachmentids)

  }

  const dateFormat = (value) => {
    if (value === null || value === undefined) {
      return ''
    }
    if (value.length === 13) {
      return moment(value, 'x').format('MMM DD, YYYY')
    } else {
      return moment.unix(value).format('MMM DD, YYYY')
    }
  }

  const getListRow = (item, index) => {

    const data = formsList.find((obj) => obj.formid === item.id) ?? item
    return (
      <tr key={index}>
        <td className='text-start'>{item.name}</td>
        <td>
          {data.fileddate > 0 ? dateFormat(data.fileddate) : ''}
        </td>
        <td>
          {data?.status &&
            <Badge color={statusColArr[data.status]}> {statusArr[data.status]}</Badge>
          }
        </td>
        <td>
          {data?.files?.map((file) => {
            return (
              <Badge color='primary'> {file.name}</Badge>
            )
          })
          }
        </td>

        <td>
          <div className='column-action d-flex align-items-center width-300-per'>
            <Col
              onClick={() => { openEditModal(item) }}>
              <Edit className='cursor-pointer mt-0' size={16} />
            </Col>
            {false &&
              <Col
                onClick={() => { deletefn(data.id) }}>
                <Trash2
                  className='cursor-pointer ms-1 mt-0' size={16} />
              </Col>
            }
          </div>
        </td>
      </tr>
    )

  }

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      //Set the progress value to show the progress bar
      setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const statusOptions = [{ id: 1, name: 'Not Filed' }, { id: 2, name: 'Filed' }]

  const uploadFiles = (files) => {

    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })
    formData.append('id', id)
    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', userId)
    setBlock(true)
    axios.post('/google/taskformfolders', formData, configUpload).then((res) => {
      setAttachmentsId([...attachmentIds, ...res.data.attachment_ids])
      setBlock(false)
    }).catch((err) => { console.log(err); setBlock(false) })
  }

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: async (files, rejectedFiles) => {

      const acceptedFiles = []
      files.forEach(obj => {
        obj.extension = obj.name.split('.').pop()
        acceptedFiles.push(obj)
      })
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        await uploadFiles(acceptedFiles)
        //setFiles(acceptedFiles)
        setFilesArr([...filesArr, ...acceptedFiles])
      }
    }
  })

  const handleRemoveFile = (file, index) => {
    const uploadedFiles = filesArr
    const filtered = uploadedFiles.filter(i => i.name !== file.name)
    setAttachmentsId([...attachmentIds.splice(index, 1)])
    //setFiles(filtered)
    setFilesArr(filtered)
  }

  const fileList = filesArr.map((file, index) => (
    <Col md={6} key={`${file.name}-${index}`} className={`d-flex align-items-center justify-content-between ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
      <div className='file-details d-flex align-items-center'>
        <div>
          <p className='file-name mb-0 text-overflow-ellipsis'>{file.name.split('.')[0].length > 15 ? `${file.name.split('.')[0].substr(0, 15)}` : file.name}</p>
        </div>
      </div>
      <X size={14} className='cursor-pointer' onClick={() => handleRemoveFile(file, index)} />
    </Col>
  ))


  const onSubmit = async data => {
    data.fileddate = data.fileddate !== '' ? moment(data.fileddate).valueOf() : 0
    data.attachmentIds = attachmentIds

    if (taskFormId !== null) {
      axios.post('/taskforms/update', data)
        .then(() => {
          getFormsList()
          closeModal()
          toast.success('Form Updated Successfully', { position: 'bottom-right' })
          setFilesArr([])
          setProgress(0)
          setAttachmentsId([])
          setTaskFormId(null)
        })
        .catch((err) => { console.log(err) })
      setSelected(null)
    } else {

      axios.post('/taskforms/create', data)
        .then(() => {
          getFormsList()
          closeModal()
          setFormModal(false)
          setFilesArr([])
          setTaskFormId(null)
          setAttachmentsId([])
          toast.success('Form Added Successfully', { position: 'bottom-right' })
        })
        .catch((err) => { console.log(err) })
    }
  }

  useEffect(() => {
    if (tabId === 'taskforms') {
      getFormsList()
    }
  }, [tabId])

  const getSelectRow = (fieldLabel, fieldName, options, reqflag = false) => {
    return (

      <Col lg={12}>
        <Label sm='12' size='lg' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col sm='12'>
          <Controller
            control={control}
            name={fieldName}
            id={fieldName}
            render={({ field, ref }) => (
              <Select
                inputRef={ref}
                className={classnames('react-select', { 'is-invalid': errors[fieldName] })}
                {...field}
                classNamePrefix='select'
                options={options}
                value={options.find(c => { return c.id === field.value })}
                onChange={val => { field.onChange(val.id) }}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
              />
            )}
          />
          {errors[fieldName] && <FormFeedback className='text-danger'>{errors[fieldName]?.message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const handleRemoveFileFromLicense = (id) => {

    const tempIds = [...attachmentIds]
    tempIds.splice(tempIds.indexOf(id), 1)
    setAttachmentsId(tempIds)
    const tempfile = [...selected.files]
    const temp = tempfile.filter((ob) => { return ob.id !== id })

    const test = { ...selected }
    test.files = temp
    setSelected(test)
    setValue('attachmentIds', temp.map((obj) => obj.id) ?? [])

  }

  return (
    <Card>
      <CardBody >
        <CardTitle className='align-items-end card-title d-flex justify-content-between mb-0' tag='h5'>
          Forms
        </CardTitle>
      </CardBody>
      <Table className='text-nowrap text-center border-bottom' responsive>
        <thead>
          <tr>
            <th className='text-start'>Name</th>
            <th>Date</th>
            <th>status</th>
            <th>Attachments</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {selTask?.serviceforms?.map((item, index) => {
            return (
              getListRow(item, index)
            )
          })}
        </tbody>
      </Table>
      <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
        <ModalHeader toggle={() => closeModal()}>Forms</ModalHeader>
        <ModalBody>
          <UILoader blocking={block} loader={<Loader />}>
            {
              Object.keys(errors).map((key) => {

                return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

              })
            }
            <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>
              <Col md={12}>
                <Label className='form-label required' for='comments'>
                  Form Name
                </Label>
                <Input disabled value={formName} />
              </Col>
              {getSelectRow('Status', 'status', statusOptions, true)}

              <Col md={12}>
                <Label className='form-label' for='comments'>
                  Filing Date
                </Label>
                <Controller
                  control={control}
                  name={`fileddate`}
                  id='fileddate'
                  render={({ field }) => (
                    <Flatpickr
                      value={field.value}
                      onChange={(date, dateStr) => { field.onChange(dateStr) }}
                      options={{ altInput: true, altFormat: "M j, Y", dateFormat: "M j, Y" }}
                      className='form-control invoice-edit-input date-picker'
                    />
                  )}
                />
                {errors.fileddate && <FormFeedback>{errors.fileddate.message}</FormFeedback>}

              </Col>
              <Col>
                <input {...getInputProps()} />
                <Button color='primary' {...getRootProps({  })}>Add Attachments</Button>
              </Col>
              {fileUploadProgress > 0 && <Progress color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}
              <Row className='d-flex p-1'>{fileList}</Row>

              <Row className='d-flex col-12'>
                {selected?.files?.map((file, k) => {
                  return (<Badge key={k} color={'primary'} className='me-1 mt-1 col-4' onClick={() => handleRemoveFileFromLicense(file.id)}>
                    {file.name.split('.')[0].length > 15 ? `${file.name.split('.')[0].substr(0, 15)}` : file.name} X</Badge>)
                })
                }
              </Row>

              <Col className='mt-2 pt-1' xs={12}>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button className='me-1' color='warning' onClick={() => closeModal()}>
                  Cancel
                </Button>
              </Col>

            </Row>
          </UILoader>
        </ModalBody>
      </Modal>
    </Card>
  )
}

export default Forms
