// ** React Imports
import { useState, useEffect } from 'react'
// ** Reactstrap Imports
import {
  Card,
  CardTitle,
  CardHeader,
  CardBody,
  Row,
  Badge,
  Col
} from 'reactstrap'

// ** Styles
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useParams } from 'react-router-dom'
import axios from '@src/configs/axios/axiosConfig'
import moment from 'moment'

const Documents = ({ tabId, selTask }) => {
  // ** Store Vars
  const { id } = useParams()
  const [data, setData] = useState([])
  const statusOptions = ['', 'Pending', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']
  const statusObj = [
    '',
    'light-primary',
    'light-warning',
    'light-success',
    'light-secondary',
    'light-secondary',
    'light-ocean',
    'light-info',
    'light-danger'
  ]

  const getData = () => {

    axios.post('/taskstatustimeline/get', { id })
      .then((res) => {
        setData(res.data.timeline)
      })
      .catch((err) => { console.log(err) })

  }

  useEffect(() => {
    if (tabId === 'statustimeline') {
      getData()
    }
  }, [tabId])

  const dateFormat = (val) => {
    return moment(val, 'x').format('MMM DD, Y')
  }

  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <CardHeader className='py-1'>
          <CardTitle tag='h4'>Status Timeline</CardTitle>
        </CardHeader>
        <CardBody>
          <div className="parent-status-timeline">
            <div className="straight-flow">
              {
                data.map((obj, k) => {
                  return (
                    <div className="flow-status-holder" key={k}>
                      <div className="status-timeline  statuscol_open">
                        <div className="st_tmlne_stts_txt_span" data-tooltip="Open" ellipsis="true" aria-label="Open">
                          <Badge color={statusObj[obj.status]} >{statusOptions[obj.status]}</Badge>
                        </div>
                      </div>
                      <div className="flow-transiton-holder">
                        <div className="flow-transition-content" ellipsis="true" >{dateFormat(obj.createdon)}
                          {
                            obj.statusname !== null && <span> - <Badge>{obj.statusname} </Badge> </span>
                          }
                        </div>
                        <div className="flow-transition-content">
                          {obj.createdBy}
                        </div>
                      </div>
                    </div>
                  )
                })
              }
              {
                parseInt(selTask.taskstatus) !== 3 && (
                  <div>
                    <Badge color='secondary'>Yet to Close</Badge>
                  </div>
                )
              }
            </div>
          </div>
        </CardBody>
      </Card>
    </div >
  )
}

export default Documents
