// ** Reactstrap Imports
import { Button, Card, Col, Input, Label, Modal, ModalBody, ModalHeader, Row, Table } from 'reactstrap'

// ** Third Party Components
import { Edit, Plus, Trash } from 'react-feather'

import axios from '@src/configs/axios/axiosConfig'
// ** Styles
import classnames from 'classnames'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import '@styles/react/libs/react-select/_react-select.scss'
import moment from 'moment'
import Select from 'react-select'
import ReactPaginate from 'react-paginate'
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const dateFormattime = (value) => {
  if (value.length === 13) {
    return moment(value, 'x').format("h:m a")
  } else {
    return moment.unix(value).format("h:m a")
  }
}

const dateFormat = (value) => {
  if (value.length === 13) {
    return moment(value, 'x').format("Y MMM, DD")
  } else {
    return moment.unix(value).format("Y MMM, DD")
  }
}

const hoursFormat = (row) => {

  const decimalTimeString = row.totalhours
  let decimalTime = parseFloat(decimalTimeString)
  decimalTime = decimalTime * 60 * 60
  let hours = Math.floor((decimalTime / (60 * 60)))
  decimalTime = decimalTime - (hours * 60 * 60)
  let minutes = Math.floor((decimalTime / 60))

  if (hours < 10) {
    hours = `0${hours}`
  }
  if (minutes < 10) {
    minutes = `0${minutes}`
  }

  return (`${isNaN(hours) ? '00' : hours}Hrs : ${isNaN(minutes) ? '00' : minutes}Mins`)

}
const MySwal = withReactContent(Swal)
const Timesheets = ({ tabId }) => {

  const { id } = useParams()
  const [dataArr, setData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)

  const [startTime, setStartTime] = useState()
  const [endTime, setEndTime] = useState()
  const [hourlyRate, setHourlyRate] = useState(0)
  const [timeSheetDate, setTimeSheetDate] = useState(moment().startOf('day').unix())
  const [timeSheetTempDate, setTimeSheetTempDate] = useState(moment().startOf('day').valueOf())
  const [total, setTotal] = useState(0)
  const [formModal, setFormModal] = useState(false)
  const [eventName, setEventName] = useState('Add')
  const [selectedTimeSheet, setSelectedTimesheet] = useState(null)
  const rowsPerPage = 15
  const [workflowOptions, setWorkflowOptions] = useState([])
  const [workFlow, setWorkFlow] = useState(null)

  const getTimeSheetList = async (page) => {

    await axios.post('/tasktimesheets/list', { taskId: id, page })
      .then((res) => {
        setData(res.data.tasktimesheets.tasktimesheets)
        setTotal(res.data.tasktimesheets.total)
      })
  }

  const getTaskWorkflows = async () => {

    await axios.post('/taskworkflows/dropdown', { taskId: id })
      .then((res) => {
        setWorkflowOptions(res.data.taskworkflows)
      })
  }

  const deleteTimeSheet = async (id) => {

    await axios.post('/tasktimesheets/delete', { id, updatedBy: userId })
      .then(() => {
        getTimeSheetList(currentPage)
      })
  }

  const MySwal = withReactContent(Swal)

  const deletefn = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await deleteTimeSheet(id)
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'TImesheet has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const handlePagination = async page => {
    setCurrentPage(page.selected + 1)
    getTimeSheetList(page.selected + 1)
  }

  const cancellModal = () => {
    setFormModal(false)
    setEventName('Add')
    setSelectedTimesheet(null)
  }

  const onSubmit = () => {
    const starttime = moment(timeSheetTempDate, 'x').set('hour', startTime.split(':')[0]).set('minutes', startTime.split(':')[1]).valueOf()
    const endtime = moment(timeSheetTempDate, 'x').set('hour', endTime.split(':')[0]).set('minutes', endTime.split(':')[1]).valueOf()
    const userid = userId
    const start = moment(timeSheetTempDate, 'x').set('hour', startTime.split(':')[0]).set('minutes', startTime.split(':')[1])
    const end = moment(timeSheetTempDate, 'x').set('hour', endTime.split(':')[0]).set('minutes', endTime.split(':')[1])
    const duration = moment.duration(end.diff(start))
    const hours = duration.asHours()
    const amount = hourlyRate * hours

    axios.post('/tasktimesheets/manualentry', { taskWorkFlowId: workFlow?.id, timeSheetDate: timeSheetTempDate, id: selectedTimeSheet?.id, createdBy: userId, starttime, endtime, userid, amount, hourlyRate, totalhours: hours, taskId: id })
      .then(() => {
        getTimeSheetList(currentPage)
        cancellModal()
      })
      .catch(() => {

      })
  }

  const getEmployeeDet = () => {
    axios.post('/organizationusers/getjobinfo', { id: userId })
      .then((res) => {
        setHourlyRate(res.data.organizationusers.hourlyrate || 0)
      }).catch((err) => {
        console.log(err)
      })
  }

  useEffect(async () => {
    if (tabId === 'timesheets') {
      getTimeSheetList(currentPage)
      getEmployeeDet()
      getTaskWorkflows()
    }
  }, [tabId])

  const CustomPagination = () => {
    const count = Number(Math.ceil(total / rowsPerPage))

    return (
      <ReactPaginate
        nextLabel=''
        breakLabel='...'
        previousLabel=''
        pageCount={count || 1}
        activeClassName='active'
        breakClassName='page-item'
        pageClassName={'page-item'}
        breakLinkClassName='page-link'
        nextLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousLinkClassName={'page-link'}
        previousClassName={'page-item prev'}
        onPageChange={page => handlePagination(page)}
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        containerClassName={'pagination react-paginate justify-content-end p-1'}
      />
    )
  }

  const openEditModal = (row) => {
    setSelectedTimesheet(row)
    setTimeSheetDate(moment(row.createdon, 'x').unix())
    setStartTime(moment(row.starttime, 'x').format('HH:mm'))
    setEndTime(moment(row.endtime, 'x').format('HH:mm'))
    setWorkFlow(workflowOptions.find(ob => ob.id === row.taskworkflowid))
    setFormModal(true)
    setEventName('Edit')
  }

  return (
    <Card className='px-2'>
      <div className='react-dataTable user-view-account-projects'>
        <Col className='d-flex justify-contents-between p-2'>
          <Col>
            <h4>Time Sheet List</h4>
          </Col>
          <Button color='primary' onClick={() => setFormModal(!formModal)} >
            <Plus size={14} /> {eventName} Logs Manually
          </Button>
        </Col>

        <Table className='table table-hover table-bordered px-2'>
          <thead>
            <tr>
              <th>User</th>
              <th>Date</th>
              <th>Start Time</th>
              <th>End Time</th>
              <th>Time Taken (HH : MM)</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {
              dataArr.map((row, k) => {
                return (<tr key={k}>
                  <td>{row.username}</td>
                  <td>{dateFormat(row.createdon)}</td>
                  <td>{dateFormattime(row.starttime)}</td>
                  <td>{(row.endtime !== null ? dateFormattime(row.endtime) : '-')}</td>
                  <td>{hoursFormat(row || '0')}</td>
                  <td>
                    <Col className='d-flex'>
                      <Edit size={14} className='me-1' onClick={() => openEditModal(row)} />
                      <Trash size={14} onClick={() => { deletefn(row.id) }} />
                    </Col>
                  </td>
                </tr>
                )
              })
            }
          </tbody>
        </Table>
        <CustomPagination />

        <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
          <ModalHeader toggle={() => setFormModal(!formModal)}>Add Timesheet Log</ModalHeader>
          <ModalBody>
            <Row>
              <Label size='lg' className='form-label'>Task Workflow</Label>
              <Col>
                <Select
                  className={classnames('react-select')}
                  classNamePrefix='select'
                  options={workflowOptions}
                  value={workFlow}
                  onChange={val => setWorkFlow(val)}
                  getOptionLabel={(option) => option.name}
                  getOptionValue={(option) => option.id}
                />
              </Col>
            </Row>
            <Row>
              <Label size='lg' className='form-label required'>Date</Label>
              <Col>
                <Flatpickr
                  onChange={(date, dateStr) => { setTimeSheetTempDate(moment.unix(dateStr).startOf('day').valueOf()) }}
                  options={{ altInput: true, dateFormat: "U" }}
                  value={String(timeSheetDate)}
                  className={classnames('due-date-picker')} />
              </Col>
            </Row>
            <Row>
              <Label size='lg' className='form-label required'>Start Time</Label>
              <Col><Input type='time' value={startTime} onChange={(e) => { setStartTime(e.target.value) }} /></Col>

              <Label size='lg' className='form-label required'>End Time</Label>
              <Col>
                <Input type='time' value={endTime} onChange={(e) => { setEndTime(e.target.value) }} />
              </Col>

            </Row>
            <Row className='d-flex justify-content-between mt-1'>
              <Col>
              </Col>
              <Col>
                <Button color='warning' onClick={() => { cancellModal() }}>Cancel</Button>
                <Button color='primary' className='ms-1' onClick={() => { onSubmit() }}>Submit</Button>
              </Col>
            </Row>
          </ModalBody>

        </Modal>

      </div>
    </Card>
  )
}

export default Timesheets
