import { useEffect, useState, Fragment } from 'react'
import { useDispatch } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom'
import { checklists, addTaskWorkflow, updateTaskWorkflow } from '../store/index'
// ** Reactstrap Imports
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import { Card, CardBody, Form, UncontrolledDropdown, DropdownToggle, DropdownItem, DropdownMenu, Row, Input, Button, Col, Progress, ListGroupItem, Badge, Modal, ModalHeader, ModalBody, Table, FormFeedback, Label } from 'reactstrap'
import { CheckCircle, MoreVertical, RefreshCcw, Paperclip, X, Plus, Eye, Edit, Trash, User, MessageSquare } from 'react-feather'
import axios from '@src/configs/axios/axiosConfig'
import { orgUserId, activeOrganizationid, activeOrganization } from '@src/helper/sassHelper'

const userId = orgUserId()
const activeOrg = activeOrganization()
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
const activeOrgId = activeOrganizationid()
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { Editor } from 'react-draft-wysiwyg'
import draftToHtml from 'draftjs-to-html'
import { convertToRaw, ContentState, convertFromHTML, EditorState } from 'draft-js'
import '@styles/react/libs/editor/editor.scss'
import classnames from 'classnames'
import { ReactSortable } from 'react-sortablejs'

import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import toast from 'react-hot-toast'
import moment from 'moment'
const MySwal = withReactContent(Swal)

const CheckList = ({ tabId, switchSubTaskView }) => {
  const [block, setBlock] = useState(false)
  const navigate = useNavigate()
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const [formModal, setFormModal] = useState(false)
  //const [files, setFiles] = useState([])
  const [addWorflow, setAddWorkflow] = useState(false)
  const [userOptions, setuserOptions] = useState([])
  const { id } = useParams()
  const dispatch = useDispatch()
  const [data, setData] = useState([])
  const [editorData, setEditorData] = useState('')
  const [selData, setSelData] = useState(null)
  const [userids, setUserIds] = useState([])

  const [selctedIds, setSelctedIds] = useState([])

  const colourStyles = {
    multiValue: (styles, { }) => {

      return {
        ...styles,
        backgroundColor: '#7367f0'
      }
    }
  }

  const schema = yup.object().shape({
    createdBy: yup.string().default(userId),
    updatedBy: yup.string().default(userId),
    name: yup.string().required("Please Enter Name"),
    organizationId: yup.string().default(activeOrgId),
    taskId: yup.string().default(id),
    id: yup.string().default('').nullable(),
    description: yup.string(),
    position: yup.string().default(0)
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data.organizationusers
      setuserOptions(arr)
    }).catch((err) => {
      console.log(err)
    })
  }

  const getChecklists = async () => {
    setBlock(true)
    const res = await dispatch(checklists({ taskId: id }))
    const temp = res.payload.filter(oj => !oj.flg)
    await setData(temp)
    setBlock(false)
  }

  const tagUser = () => {
    axios.post('/taskworkflows/assignuser', { userids: userids.map((oj) => oj.id), id: selData.id, updatedBy: userId })
      .then(() => {
        getChecklists()
        setFormModal(false)
        setUserIds([])
      })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  const deleteUser = (userList, userid, workflowid) => {
    const temp = userList.map((oj) => oj.id)
    temp.splice(temp.indexOf(userid), 1)
    axios.post('/taskworkflows/assignuser', { userids: temp, id: workflowid, updatedBy: userId })
      .then(() => {
        getChecklists()
        setFormModal(false)
        setUserIds([])
      })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  const removeuser = (userList, userid, workflowid) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, Remove user!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await deleteUser(userList, userid, workflowid)
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Sub Task user has been removed.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })

  }

  const resetForm = () => {
    setAddWorkflow(false)
    setValue('id', '')
    setValue('name', '')
    setValue('description', '')
    setEditorData('')
    setAddWorkflow(false)
  }

  const onSubmit = async formdata => {
    if (formdata.id === '') {
      formdata['position'] = data.length + 1
      await dispatch(addTaskWorkflow(formdata))
    } else {
      await dispatch(updateTaskWorkflow(formdata))
    }
    resetForm()
    getChecklists()
  }

  const handleChange = (editorState) => {
    setEditorData(editorState)
    setValue('description', draftToHtml(convertToRaw(editorState.getCurrentContent())))
  }

  const assignUserModal = (val) => {
    setSelData(val)
    setFormModal(true)
  }

  useEffect(() => {
    if (tabId === 'checklist') {
      getChecklists()
      getOrganizationUsers()
    }
  }, [tabId])

  const updateStatus = (sts, id) => {
    setBlock(true)
    axios.post('/taskworkflows/updatestatus', { status: sts, id, updatedBy: userId })
      .then(() => { getChecklists(); setBlock(false) })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  const deletefun = (workflowId) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await updateStatus(-1, workflowId)
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Checklist has been Updated.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const updatePositionfn = () => {
    setBlock(true)
    axios.post('/taskworkflows/updateposition', { workflows: data })
      .then(() => {
        toast.success('Checklist sorted Successfully', { position: 'bottom-right' })
        setBlock(false)
        setSortable(true)
      })
      .catch(() => { })
  }

  const exportPdf = () => {
    axios.post('/taskworkflows/exportpdf', { taskId: id })
      .then((res) => {
        const downloadLink = document.createElement("a")
        const fileName = `checklist.pdf`
        downloadLink.href = res.data.pdf
        downloadLink.download = fileName
        downloadLink.click()
        document.removeChild(downloadLink)
      })
      .catch((err) => { console.log(err) })
  }

  const workFlowEditModal = (workflow) => {
    setAddWorkflow(true)
    setValue('id', workflow.id)
    setValue('name', workflow.title)
    const desc = workflow.comments
    setValue('description', desc)
    setValue('taskId', workflow.taskId)
    const temp = EditorState.createWithContent(
      ContentState.createFromBlockArray(
        convertFromHTML(desc)
      )
    )

    setEditorData(temp)
  }

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const statusBulkUpdate = (sts) => {

    setBlock(true)
    axios.post('/taskworkflows/updatestatusbulk', { status: sts, selctedIds, updatedBy: userId })
      .then(() => { getChecklists(); setSelctedIds([]) })
      .catch((err) => { console.log(err); setBlock(false) })

  }

  const handleCheckBox = (e, id) => {
    if (e.target.checked) {
      setSelctedIds([...selctedIds, id])
    } else {
      const temp = [...selctedIds]
      temp.pop(id)
      setSelctedIds(temp)
    }
  }

  return (
    <Card>
      <UILoader blocking={block} loader={<Loader />}>
        <CardBody className='pt-0'>
          <Col className='p-1 d-flex justify-content-between align-items-center'>
            Sub Task
            <Col>
              <UncontrolledDropdown className='ms-1'>
                <DropdownToggle color='secondary' caret outline>
                  List View
                </DropdownToggle>
                <DropdownMenu>
                  <DropdownItem onClick={() => { switchSubTaskView(true) }} className='w-100'>
                    <span className='align-middle'>List View </span>
                  </DropdownItem>
                  <DropdownItem onClick={() => { switchSubTaskView(false) }} className='w-100'>
                    <span className='align-middle'>Detailed View</span>
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Col>
            <Col className='d-flex col-md-4 align-items-center justify-content-end'>
              {selctedIds.length > 0 &&
                <Button color='success' className='me-1' outline onClick={() => statusBulkUpdate(3)}>Mark as Complete</Button>
              }
              <Button color='primary' className='me-1' onClick={() => setAddWorkflow(true)}><Plus size={14} /> Add</Button>
              <Button outline color='primary' onClick={() => { exportPdf() }}>Export</Button>
            </Col>
          </Col>
          <Col className='p-1'>

            <Table responsive>
              <thead>
                <tr>
                  <th></th>
                  <th>Steps</th>
                  <th>Sub Task Name</th>
                  <th>Description</th>
                  <th>Assignee</th>
                  <th>Status</th>
                  <th>Completion</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <ReactSortable tag='tbody' list={data} setList={setData} onSort={() => { updatePositionfn() }}>
                {data?.map((item) => {
                  return (
                    <tr className='cursor-pointer' key={item?.id}>
                      <td>
                        {parseInt(item.progressstatus) === 1 &&
                          <Input type='checkbox' className='me-4' onChange={(e) => { handleCheckBox(e, item.id) }} />
                        }
                      </td>
                      <td className='p-1' onClick={() => navigate(`/taskworkflow/view/${item.id}`)}>{item?.position}</td>
                      <td className='p-1' onClick={() => navigate(`/taskworkflow/view/${item.id}`)}>{item?.title?.length > 39 ? `${item?.title.substr(0, 40)}` : item?.title}</td>
                      <td className='p-1' onClick={() => navigate(`/taskworkflow/view/${item.id}`)}>
                        <div className='white-space-wrap' dangerouslySetInnerHTML={{ __html: item?.comments?.replace(/(<([^>]+)>)/ig, '').substr(0, 40) }} >
                        </div>
                      </td>
                      <td className='p-1'>
                        {
                          item?.users?.map((user) => {
                            return (
                              <Badge color='light-primary' key={user.id} >{user.name}
                                <X size={14} className='ms-50 cursor-pointer' onClick={() => removeuser(item?.users, user.id, item.id)} />
                              </Badge>
                            )
                          })
                        }
                      </td>
                      <td className='p-1'>
                        {item.progressstatus === '1' ? <Badge color='light-primary'>Progress</Badge> : <Badge color='light-success'>Completed</Badge>}
                      </td>
                      <td className='p-1'>{
                        item.completedon ? moment(item.completedon, 'x').format('MMM D, Y') : ''
                      }</td>
                      <td className='text-end p-1'>
                        <Col className='d-flex align-items-center'>
                          <UncontrolledDropdown>
                            <DropdownToggle tag='div' className='btn btn-sm p-0 ms-25'>
                              <MoreVertical size={14} className='cursor-pointer' />
                            </DropdownToggle>
                            <DropdownMenu>
                              {(parseInt(activeOrg.rolelevel) === 1 || parseInt(activeOrg.rolelevel) === 2) &&
                                <Fragment>
                                  <DropdownItem onClick={() => workFlowEditModal(item)}>
                                    <Edit size={14} /> Edit
                                  </DropdownItem>
                                  <DropdownItem onClick={() => deletefun(item?.id)}>
                                    <Trash size={14} /> Delete
                                  </DropdownItem>
                                </Fragment>
                              }
                              <DropdownItem onClick={() => assignUserModal(item)}>
                                <User size={14} /> Assign User
                              </DropdownItem>
                              {item?.progressstatus === '1' &&
                                <DropdownItem onClick={() => updateStatus(3, item?.id)}>
                                  <CheckCircle size={14} /> Mark as Complete
                                </DropdownItem>
                              }
                              {item?.progressstatus === '3' &&
                                <DropdownItem onClick={() => updateStatus(1, item?.id)}>
                                  <RefreshCcw size={14} /> Reopen
                                </DropdownItem>
                              }
                            </DropdownMenu>
                          </UncontrolledDropdown>
                        </Col>

                      </td>
                    </tr>
                  )
                })}
              </ReactSortable>
            </Table>
          </Col>

          <Modal isOpen={addWorflow} toggle={() => setAddWorkflow(!addWorflow)} className='modal-dialog-centered'>
            <ModalHeader toggle={() => setAddWorkflow(false)}>Add Sub Task</ModalHeader>
            <ModalBody>
              {
                Object.keys(errors).map((key) => {
                  return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>
                })
              }
              <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>
                {getRow('Sub Task Name', 'name', true)}
                <Col md={12}>
                  <Label className='form-label' for='description'>
                    Description
                  </Label>
                  <div id='message-editor'>
                    <Editor
                      placeholder='Message'
                      toolbarClassName='rounded-0'
                      wrapperClassName='toolbar-bottom'
                      editorState={editorData}
                      onEditorStateChange={handleChange}
                      editorClassName='rounded-0 border-1'
                    />
                  </div>
                  {errors.description && <FormFeedback>{errors.description.message}</FormFeedback>}
                </Col>

                <Col className='mt-2 pt-1' xs={12}>
                  <Button type='submit' className='me-1' color='primary'>
                    Submit
                  </Button>
                  <Button className='me-1' color='warning' onClick={() => resetForm()}>
                    Cancel
                  </Button>
                </Col>

              </Row>
            </ModalBody>
          </Modal>

          <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
            <UILoader blocking={block} loader={<Loader />}>
              <ModalHeader toggle={() => setFormModal(!formModal)}>Bulk Task Re Assign</ModalHeader>
              <ModalBody>
                <Row>
                  <Col>
                    <Select
                      isClearable={false}
                      value={userids}
                      options={userOptions}
                      className='react-select'
                      classNamePrefix='select'
                      isMulti
                      theme={selectThemeColors}
                      getOptionLabel={(option) => option.name}
                      getOptionValue={(option) => option.id}
                      styles={colourStyles}
                      onChange={async data => {
                        setUserIds(data)
                      }}
                    />
                  </Col>
                </Row>

                <Row className='d-flex justify-content-between mt-1'>
                  <Col>
                  </Col>
                  <Col>
                    <Button color='warning' onClick={() => { setFormModal(false) }}>Cancel</Button>
                    <Button color='primary' className='ms-1' onClick={() => { tagUser() }}>Submit</Button>
                  </Col>
                </Row>
              </ModalBody>
            </UILoader>
          </Modal>
        </CardBody>
      </UILoader>
    </Card>
  )
}

export default CheckList
