// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import { Link, useLocation, useParams } from 'react-router-dom'
// ** Reactstrap Imports
import { useDropzone } from 'react-dropzone'
import { Can } from '@src/utility/context/Can.js'
import { Row, Col, Card, CardBody, Badge, Table, Nav, NavItem, ListGroup, ListGroupItem, NavLink, Input, TabContent, TabPane, Button, Popover, PopoverHeader, PopoverBody, UncontrolledTooltip, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Label, Progress, CardHeader, ModalBody, Modal, InputGroup, ModalHeader } from 'reactstrap'
// ** Third Party Components
import Conversation from './Conversation'
import CheckList from './CheckList'
import TimeSheet from './TimeSheet'
import CheckListTable from './CheckListTable'
import Templates from './Templates'
import InvoiceDetails from './InvoiceDetails'
import Forms from './Forms'
import Documents from './Documents'
import StatusTimeline from './StatusTimeline'

import { Bell, Calendar, Clock, Edit, FileText, MessageCircle, User, X, Paperclip, RefreshCcw, MoreVertical, FilePlus, File, Plus } from 'react-feather'
import Select from 'react-select'
import '@styles/react/libs/react-select/_react-select.scss'
// ** Custom Components
import toast from 'react-hot-toast'
import axios from '@src/configs/axios/axiosConfig'
import { startTimer, endTimer, updateStatusRefresh } from '../store/index'
import { addTaskConversation } from '../store/taskconversation'
// ** Styles
import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment'
import { orgUserId, activeOrganizationid, activeOrganization } from '@src/helper/sassHelper'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const userId = orgUserId()
const activeOrg = activeOrganization()
const activeOrgId = activeOrganizationid()

const priorityColors = [
  '',
  'light-success',
  'light-warning',
  'light-danger'
]

const priorityArr = [
  '',
  'Low',
  'Medium',
  'High'
]

const UserInfoCard = ({ selectedTask }) => {

  const statusOptions = ['', 'Pending', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']
  const statusObj = [
    '',
    'light-primary',
    'light-warning',
    'light-success',
    'light-secondary',
    'light-secondary',
    'light-ocean',
    'light-info',
    'light-danger'
  ]

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const { id } = useParams()

  const search = useLocation().search
  const searchParams = new URLSearchParams(search)

  // ** State
  const dispatch = useDispatch()
  const [active, setActive] = useState('tasknotes')
  const [popoverOpen, setPopoverOpen] = useState(false)
  const [popoverOpen1, setPopoverOpen1] = useState(false)
  const [timerFlag, setTimerFlag] = useState(false)
  const [hourlyRate, setHourlyRate] = useState(0)
  const [taskDescription, setTaskDescription] = useState('')
  const [activeStatus, setActiveStatus] = useState(3)
  const [comment, setComment] = useState('')

  const toggleTab = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  useEffect(() => {
    const tabId = searchParams.get('tabId')
    if (tabId) {
      setActive(tabId)
    }
  }, [searchParams])

  const [assigneeStatus, setAssigneeSts] = useState(false)
  const [reviewerStatus, setReviewerSts] = useState(false)
  const [attachmentIds, setAttachmentsId] = useState([])
  const [fileUploadProgress, setProgress] = useState(0)

  const [filesArr, setFilesArr] = useState([])
  const [formModal, setFormModal] = useState(false)
  const [reason, SetReason] = useState('')
  const [listview, setListView] = useState(false)

  const fileClassNames = {
    image: "ui-attachment--image",
    pdf: "ui-attachment--pdf",
    xlsx: "ui-attachment--excel",
    xls: "ui-attachment--excel",
    docx: "ui-attachment--docx",
    doc: "ui-attachment--docx"
  }

  const store = useSelector(state => state.task)
  useEffect(() => {
    const tabId = searchParams.get('tabId')
    if (tabId) {
      setActive(tabId)
      const viewVal = localStorage.getItem('subTaskView')
      setListView(viewVal ?? false)
    }
  }, [searchParams])

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }
  }

  const getDescription = () => {
    axios.post('/taskconversations/description', { taskId: id })
      .then((res) => {
        setTaskDescription(res.data.taskconversation === undefined ? '' : res.data.taskconversation.comment)
      }).catch((err) => {
        console.log(err)
      })
  }
  const [statusList, setStatusList] = useState([])
  const [subStatusValue, setSubStatusValue] = useState(null)
  const getSubStatus = () => {
    axios.post('/taskstatus/dropdown', { id: selectedTask.serviceid, expectFlg: true })
      .then((res) => {
        setStatusList(res.data.taskstatus || [])
      }).catch((err) => {
        console.log(err)
      })
  }

  const getEmployeeDet = () => {
    axios.post('/organizationusers/getjobinfo', { id: userId })
      .then((res) => {
        setHourlyRate(res.data.organizationusers.hourlyrate || 0)
        getSubStatus()
      }).catch((err) => {
        console.log(err)
      })
  }

  const postComment = async () => {

    setBlock(true)
    const data = {
      organizationId: activeOrgId,
      createdBy: userId,
      updatedBy: userId,
      comment,
      attachmentIds,
      taskId: id
    }
    await dispatch(addTaskConversation(data))
    toast.success('Task Status Changed Successfully', { position: 'bottom-right' })
    setBlock(false)

  }

  const startTaskTimer = async () => {
    const data = {
      createdBy: userId,
      taskId: id,
      userId,
      hourlyRate,
      organizationId: activeOrgId,
      startTime: moment().valueOf(),
      existStatus: selectedTask.taskstatus
    }
    const res = await dispatch(startTimer(data))
    localStorage.setItem('currentTask', JSON.stringify(res.payload))
  }

  const stopTaskTimer = async () => {
    const temp = JSON.parse(localStorage.getItem('currentTask') || null)
    if (temp !== null) {
      const end = moment()
      const start = moment(temp.starttime, 'x').toDate()
      const end_timestamp = moment().valueOf()
      const duration = moment.duration(end.diff(start))
      const hours = duration.asHours()
      const amount = hourlyRate * hours
      const data = {
        updatedBy: userId,
        id: temp.id,
        taskId: id,
        totalhours: hours,
        amount,
        hourlyRate,
        userId,
        endTime: end_timestamp
      }
      await dispatch(endTimer(data))
      await localStorage.removeItem('currentTask')
      await setTimerFlag(false)
    }
  }

  const assigneeSts = () => {
    const assigneeIndex = selectedTask.assignee.findIndex(obj => obj.id === userId)
    setAssigneeSts(assigneeIndex > -1 || (activeOrg.organizationusersusertypeid === '1' || selectedTask.reviewer.length === 0))
  }

  const reviewerSts = () => {
    const reviewerIndex = selectedTask.reviewer.findIndex(obj => obj.id === userId)
    const user = selectedTask.userlists.find((obj) => obj.id === userId)
    let tempflg = false
    if (selectedTask.createdby === userId) {
      tempflg = true
    } else if (parseInt(user?.type) === 1 && activeOrg.taskapproval.includes('4')) {
      tempflg = true
    } else if (parseInt(user?.type) === 2 && activeOrg.taskapproval.includes('3')) {
      tempflg = true
    } else tempflg = false

    setReviewerSts(reviewerIndex > -1 || (activeOrg.organizationusersusertypeid === '1' || selectedTask.reviewer.length === 0 || tempflg))
  }

  const updateTaskStatus = async (status, substatus = null) => {
    const data = {
      updatedBy: userId,
      taskId: id,
      status,
      existStatus: selectedTask.taskstatus,
      reason,
      substatus
    }
    setBlock(true)

    axios.post('/tasks/updatestatus', data)
      .then(async () => {
        toast.success('Task Status Updated SuccesFully', { position: 'bottom-right' })
        setPopoverOpen(false)
        setPopoverOpen1(false)
        await dispatch(updateStatusRefresh(data))
        setBlock(false)
        setFormModal(false)
        if (comment !== '' || attachmentIds.length > 0) {
          await postComment()
        }
      })
      .catch(() => {
        toast.error('Please Finish All Checklist before Sent to Review or Complete', { position: 'bottom-right' })
        setBlock(false)
      })
  }

  useEffect(() => {
    if (selectedTask !== null) {
      getEmployeeDet()
      getDescription()

      reviewerSts()
      assigneeSts()

      if (parseInt(selectedTask.taskstatus) === '6') {
        setActiveStatus('3')
      }
    }
    setSubStatusValue(selectedTask.substatus)

  }, [selectedTask])

  useEffect(() => {
    const temp = JSON.parse(localStorage.getItem('currentTask'))
    if (store.selectedTaskTimer !== null) {
      setTimerFlag(true)
    } else if (temp !== null) {
      setTimerFlag(true)
    } else {
      setTimerFlag(false)
    }
  }, [store.selectedTaskTimer])

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      //Set the progress value to show the progress bar
      setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const uploadFiles = (files) => {

    setBlock(true)
    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })
    formData.append('id', id)
    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', userId)

    axios.post('/google/taskfolders', formData, configUpload).then((res) => {
      setAttachmentsId([...attachmentIds, ...res.data.attachment_ids])
      //setFiles([])
      setBlock(false)
    }).catch((err) => {
      console.log(err)
      setBlock(false)
    })
  }

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: async (files, rejectedFiles) => {

      const acceptedFiles = []
      files.forEach(obj => {
        obj.extension = obj.name.split('.').pop()
        acceptedFiles.push(obj)
      })
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        await uploadFiles(acceptedFiles)
        //setFiles(acceptedFiles)
        setFilesArr([...filesArr, ...acceptedFiles])
      }
    }
  })

  const handleRemoveFile = (file, index) => {
    const uploadedFiles = filesArr
    const filtered = uploadedFiles.filter(i => i.name !== file.name)
    setAttachmentsId([...attachmentIds.splice(index, 1)])
    //setFiles(filtered)
    setFilesArr(filtered)
  }

  const fileList = filesArr.map((file, index) => (
    <Col md={5} key={`${file.name}-${index}`} className={`d-flex align-items-center justify-content-between ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
      <div className='file-details d-flex align-items-center'>
        <div>
          <p className='file-name mb-0 text-overflow-ellipsis'>{file.name.split('.')[0].length > 15 ? `${file.name.split('.')[0].substr(0, 15)}` : file.name}</p>
        </div>
      </div>
      <X size={14} className='cursor-pointer' onClick={() => handleRemoveFile(file, index)} />
    </Col>
  ))

  const taskComplete = () => {

    return ((reviewerStatus && selectedTask.taskstatus === '6' && selectedTask.reviewer.length > 0) || ((selectedTask.taskstatus !== '3' || selectedTask.createdby === userId) && (selectedTask.reviewer.length === 0)))
  }

  const taskReview = () => {
    return (assigneeStatus && (selectedTask.taskstatus === '1' || selectedTask.taskstatus === '2' || selectedTask.taskstatus === '7' || selectedTask.taskstatus === '4' || selectedTask.taskstatus === '5') && selectedTask.taskstatus !== '8')
  }

  const cancelPopover = (val) => {
    setFormModal(true)
    setActiveStatus(val)
  }

  const switchSubTaskView = (val) => {
    setListView(val)
    localStorage.setItem('subTaskView', val)
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardBody>
            <Table responsive bordered>
              <thead>
                <tr >
                  <th colSpan='3'>
                    <Col tag={Link} to={`/clients/view/${selectedTask.clientid}`}>{selectedTask.clientname} - </Col>
                    <Col tag={Link} to={`/services/view/${selectedTask.serviceid}`}>{selectedTask.servicename}</Col></th>
                  <th>
                    <Row className='justify-content-end'>
                      <Col md={4}>
                        {(!selectedTask.timesheetFlag && parseInt(selectedTask.taskstatus) !== 3) &&
                          <Button size='sm' disabled={timerFlag} outline color='success' onClick={() => { startTaskTimer() }}>
                            <Clock size={14} /> Start Timer</Button>
                        }
                        {(selectedTask.timesheetFlag) &&
                          <Button size='sm' outline color='danger' onClick={() => { stopTaskTimer() }}>
                            <Clock size={14} /> Stop Timer</Button>
                        }
                      </Col>
                      <Col className='p-0' md={5}>
                        {(!selectedTask.timesheetFlag) &&
                          <Col className='d-flex'>
                            <Fragment>

                              <Col>

                                {taskReview() && (
                                  <Button.Ripple size='sm' outline color='primary' id='taskcompetedpopover'> Send for Review</Button.Ripple>
                                )}
                                {(taskReview() && document.getElementById("taskcompetedpopover")) &&
                                  <Popover
                                    placement='bottom'
                                    target='taskcompetedpopover'
                                    isOpen={popoverOpen}
                                    toggle={() => setPopoverOpen(!popoverOpen)}
                                  >
                                    <PopoverHeader>
                                      <Row className='d-flex justify-content-between'>
                                        <Col>Task Send for Review</Col>
                                        <Col className='d-flex justify-content-between'>
                                          <div></div>
                                          <X className='cursor-pointer' size={14} onClick={() => setPopoverOpen(!popoverOpen)} />
                                        </Col>
                                      </Row>
                                    </PopoverHeader>
                                    <PopoverBody>
                                      <Input type='textarea' className='mb-1' cols='100' rows='4' onInput={(e) => setComment(e.target.value)} />

                                      <p className="text-danger">Please Write your comment about changes.</p>

                                      <input {...getInputProps()} />

                                      <Button type='button' color='primary' size='sm' {...getRootProps({ className: 'dropzone me-1' })} >
                                        <Paperclip size={12} className=''></Paperclip>
                                      </Button>
                                      <Button size='sm' color='primary' onClick={() => updateTaskStatus('6')}>Send for Review</Button>

                                      {fileUploadProgress > 0 && <Progress className='mt-md-50' color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}

                                      <Row className='d-flex p-1'>{fileList}</Row>
                                    </PopoverBody>
                                  </Popover>
                                }
                              </Col>
                              <Col className='ms-25'>
                                {taskComplete() && (
                                  <Button.Ripple size='sm' outline color='success' id='taskcompletedpopover2'> Mark as Complete</Button.Ripple>
                                )}
                                {(taskComplete() && document.getElementById("taskcompletedpopover2")) && (
                                  <Popover
                                    placement='bottom'
                                    target='taskcompletedpopover2'
                                    isOpen={popoverOpen1}
                                    toggle={() => setPopoverOpen1(!popoverOpen1)}
                                  >
                                    <PopoverHeader>
                                      <Row className='d-flex justify-content-between'>
                                        <Col>Task Submit</Col>
                                        <Col className='d-flex justify-content-between'>
                                          <div></div>
                                          <X className='cursor-pointer' size={14} onClick={() => setPopoverOpen1(!popoverOpen1)} />
                                        </Col>
                                      </Row>
                                    </PopoverHeader>
                                    <PopoverBody>
                                      <Input type='textarea' className='mb-1' cols='100' rows='4' onInput={(e) => setComment(e.target.value)} />

                                      <p className="text-danger">Please Select Any one option.</p>
                                      <div>
                                        <Col><Input type='radio' name='sts' value={3} defaultChecked={taskComplete()} onChange={() => setActiveStatus(3)} /> Approved</Col>
                                        <p className="text-sm mt-md-50">Submit feedback and Mark as Complete.</p>
                                      </div>
                                      <div>
                                        <Col><Input type='radio' name='sts' value={7} onChange={() => setActiveStatus(7)} /> Request changes</Col>
                                        <p className="text-sm mt-md-50">Submit feedback that must be addressed before Approval.</p>
                                      </div>
                                      <input {...getInputProps()} />

                                      <Button type='button' color='primary' size='sm' {...getRootProps({ className: 'dropzone me-1' })} >
                                        <Paperclip size={12} className=''></Paperclip>
                                      </Button>
                                      <Button size='sm' color='success' onClick={() => updateTaskStatus(activeStatus)}>Submit</Button>

                                      {fileUploadProgress > 0 && <Progress className='mt-md-50' color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}

                                      <Row className='d-flex p-1'>{fileList}</Row>
                                    </PopoverBody>
                                  </Popover>
                                )}
                              </Col>
                            </Fragment>
                          </Col>
                        }
                      </Col>
                      <Col md={3} className='d-flex'>
                        <Can I={'write'} a={'task'}>
                          <Col tag={Link} to={`/task/edit/${id}`}>
                            <UncontrolledTooltip placement='top' target='edittooltip'>
                              Edit
                            </UncontrolledTooltip>
                            <Edit size={16} className='ms-1 cursor-pointer' id='edittooltip' />
                          </Col>
                        </Can>
                        {(parseInt(activeOrg.rolelevel) === 1 || parseInt(activeOrg.rolelevel) === 2) &&
                          <Fragment>
                            {(selectedTask.taskstatus === '3' || selectedTask.taskstatus === '4' || selectedTask.taskstatus === '5') &&
                              <Col>
                                <UncontrolledTooltip placement='top' target='reopen'>
                                  Reopen
                                </UncontrolledTooltip>

                                <RefreshCcw size={16} id='reopen' className='text-primary ms-1 cursor-pointer' onClick={() => updateTaskStatus(2)} />

                              </Col>
                            }
                          </Fragment>
                        }
                        <Col  >
                          <UncontrolledDropdown >
                            <DropdownToggle tag='div' className='btn btn-sm p-0 ps-75'>
                              <MoreVertical size={16} className='cursor-pointer text-primary' />
                            </DropdownToggle>

                            <DropdownMenu>
                              {selectedTask.taskstatus !== '4' &&
                                <DropdownItem onClick={() => cancelPopover(4)}>
                                  <Label>OnHold</Label>
                                </DropdownItem>
                              }
                              {selectedTask.taskstatus !== '5' &&
                                <DropdownItem onClick={() => cancelPopover(5)}>
                                  <Label>Cancel</Label>
                                </DropdownItem>
                              }

                            </DropdownMenu>
                          </UncontrolledDropdown>

                          <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered modal-sm'>
                            <ModalHeader className='bg-primary' toggle={() => setFormModal(!formModal)} >
                              <h5 className='text-white'>Task {statusOptions[activeStatus]}</h5></ModalHeader>
                            <ModalBody>
                              <Col className=''>
                                <Label className='required'>Reason</Label>
                                <Input type='textarea' rows={3} cols={25} value={reason} onChange={(e) => { SetReason(e.target.value) }} />
                                <Col className='d-flex justify-content-between mt-1'>
                                  <Col></Col>
                                  <Button outline color={'primary'} onClick={() => { updateTaskStatus(activeStatus) }}>Submit</Button>
                                </Col>
                              </Col>
                            </ModalBody>
                          </Modal>
                        </Col>
                        <Col tag={Link} to={`/task/list`} >
                          <X size={16} className='ms-1 cursor-pointer' id='deletetooltip' />
                        </Col>
                      </Col>
                    </Row>
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>Task ID</td>
                  <td>{selectedTask.uniqueidentity}</td>
                  <td >
                    Priority
                  </td>
                  <td> <Badge className='text-capitalize' color={priorityColors[selectedTask.priority]}>
                    {priorityArr[selectedTask.priority]}
                  </Badge></td>
                </tr>
                <tr>
                  <td >
                    Status
                  </td>
                  <td> <Badge className='text-capitalize' color={statusObj[selectedTask.taskstatus]}>
                    {statusOptions[selectedTask.taskstatus]}
                  </Badge></td>
                  <td >
                    Sub Status
                  </td>
                  <td>
                    <Col className='d-flex'>
                      {statusList.length > 0 &&
                        <Select
                          className={'col-md-8 react-select'}
                          classNamePrefix='select'
                          options={statusList}
                          value={statusList.find(c => { return c.id === subStatusValue })}
                          onChange={async val => { setSubStatusValue(val.id); await updateTaskStatus(selectedTask.taskstatus, val.id) }}
                          getOptionLabel={(option) => option.statusname}
                          getOptionValue={(option) => option.id}
                        />
                      }
                      <Button size='sm' className='ms-1' tag={Link} to={`/setting/preference`}>
                        <Plus size={15} /> Sub Status
                      </Button>
                    </Col>
                  </td>
                </tr>
                <tr>
                  <td >
                    Assigned To
                  </td>
                  <td>
                    {selectedTask.userlists.map((user, key) => {

                      return (parseInt(user.type) === 1 && <Badge color='light-primary' pill href='#' key={key}>
                        <User size={14} />
                        <span className='align-middle ms-50'>{user.name}</span>
                      </Badge>
                      )
                    })
                    }
                  </td>
                  <td colSpan='1' rowSpan='4'>Description</td>
                  <td rowSpan='4' width={500}>
                    <div className='white-space-wrap'>
                      {taskDescription !== undefined ? taskDescription.replace(/(\\n)/g, "\n") : ''}
                    </div>
                  </td>
                </tr>
                <tr>
                  <td>Reviewer</td>
                  <td >
                    {selectedTask.userlists.map((user, key) => {

                      return (parseInt(user.type) === 2 && <Badge color='light-primary' pill href='#' key={key}>
                        <User size={14} />
                        <span className='align-middle ms-50'>{user.name}</span>
                      </Badge>
                      )
                    })
                    }
                  </td>

                </tr>
                <tr>
                  <td>Start Date</td>
                  <td >
                    <div className='d-flex justify-content-between' >
                      {dateFormat(selectedTask.startdate)}
                      <Calendar size={16} />
                    </div>
                  </td>
                </tr>
                <tr>
                  <td >
                    End Date
                  </td>
                  <td>
                    <div className='d-flex justify-content-between' >
                      {dateFormat(selectedTask.enddate)}
                      <Calendar size={16} />
                    </div>
                  </td>
                </tr>
              </tbody>
            </Table>
          </CardBody>
        </Card>
        <Nav pills className="flex-lg-row flex-column nav nav-left">
          <NavItem>
            <NavLink active={active === 'tasknotes'} onClick={() => toggleTab('tasknotes')}>
              <MessageCircle className='font-medium-3 me-50' />
              <span className='fw-bold'>Comments</span>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink active={active === 'taskdocuments'} onClick={() => toggleTab('taskdocuments')}>
              <File className='font-medium-3 me-50' />
              <span className='fw-bold'>Attachments</span>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink active={active === 'taskservicetemplate'} onClick={() => toggleTab('taskservicetemplate')}>
              <FileText className='font-medium-3 me-50' />
              <span className='fw-bold'>Templates</span>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink active={active === 'checklist'} onClick={() => toggleTab('checklist')}>
              <Bell className='font-medium-3 me-50' />
              <span className='fw-bold'>Sub Task</span>
            </NavLink>
          </NavItem>
          <Can I='read' a='time-sheet'>
            <NavItem>
              <NavLink active={active === 'timesheets'} onClick={() => toggleTab('timesheets')}>
                <Clock className='font-medium-3 me-50' />
                <span className='fw-bold'>Timesheet</span>
              </NavLink>
            </NavItem>
          </Can>
          <Can I='read' a='invoice'>
            <NavItem>
              <NavLink active={active === 'invoice_details'} onClick={() => toggleTab('invoice_details')}>
                <FilePlus className='font-medium-3 me-50' />
                <span className='fw-bold'>Invoice</span>
              </NavLink>
            </NavItem>
          </Can>
          <NavItem>
            <NavLink active={active === 'statustimeline'} onClick={() => toggleTab('statustimeline')}>
              <File className='font-medium-3 me-50' />
              <span className='fw-bold'>Status TimeLine</span>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink active={active === 'taskforms'} onClick={() => toggleTab('taskforms')}>
              <File className='font-medium-3 me-50' />
              <span className='fw-bold'>Forms</span>
            </NavLink>
          </NavItem>
        </Nav>
        <Card>
          <CardBody className='px-0'>
            <Row className=''>
              <Fragment>
                <TabContent activeTab={active}>
                  <TabPane tabId='tasknotes'>
                    <Conversation tabId={active} />
                  </TabPane>
                  <TabPane tabId='taskdocuments'>
                    <Documents tabId={active} selectedTask={selectedTask} />
                  </TabPane>
                  <TabPane tabId='taskservicetemplate'>
                    <Templates tabId={active} selectedTask={selectedTask} />
                  </TabPane>
                  <TabPane tabId='checklist'>

                    {!listview ? <CheckList switchSubTaskView={switchSubTaskView} tabId={active} /> : <CheckListTable tabId={active} switchSubTaskView={switchSubTaskView} />}
                  </TabPane>
                  <TabPane tabId='timesheets'>
                    <TimeSheet tabId={active} selectedTask={selectedTask} />
                  </TabPane>
                  <TabPane tabId='invoice_details'>
                    <InvoiceDetails tabId={active} selectedTask={selectedTask} />
                  </TabPane>
                  <TabPane tabId='statustimeline'>
                    <StatusTimeline tabId={active} selTask={selectedTask} />
                  </TabPane>
                  <TabPane tabId='taskforms'>
                    <Forms tabId={active} selTask={selectedTask} />
                  </TabPane>
                </TabContent>
              </Fragment>
            </Row>
          </CardBody>
        </Card>
      </UILoader>
    </Fragment >
  )
}

export default UserInfoCard
