// ** Reactstrap Imports
import { Badge, Button, Card, CardHeader, Col, Input, Modal, ModalBody, Row, Table } from 'reactstrap'

// ** Styles
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import axios from '@src/configs/axios/axiosConfig'
import toast from 'react-hot-toast'
import { Plus } from 'react-feather'

import { orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
const userId = orgUserId()

const paymentstatusObj = [
  { code: '5', name: "Pending", color: 'light-danger' },
  { code: '6', name: "Partially Paid", color: 'light-warning' },
  { code: '7', name: 'Paid', color: 'light-success' },
  { code: '11', name: 'Sent', color: 'light-secondary' }
]

const renderStatus = (val) => {
  const obj = paymentstatusObj.find((o) => parseInt(o.code) === parseInt(val))
  return obj
}

const dateFormatOnly = (value) => {
  if (value.length === 13) {
    return moment(value, 'x').format("MMM DD, YYYY")
  } else {
    return moment.unix(value).format("MMM DD, YYYY")
  }
}

const InvoiceDetails = ({ tabId, selectedTask }) => {

  const { id } = useParams()
  const [formModal1, setFormModal1] = useState(false)
  const [invoices, setInvoices] = useState([])
  const [invoice, setInvoice] = useState(null)
  const [invoiceId, setInvoiceId] = useState(null)

  const closeModal1 = () => {
    setFormModal1(false)
  }

  const getLinkedInvoice = (id) => {

    axios.post('/taskinvoices/get', { id })
      .then((res) => {
        if (res.data.taskinvoices !== undefined) {
          setInvoice(res.data.taskinvoices)
        }
      })
      .catch((err) => { console.log(err) })
  }

  const linkInvoice = () => {
    axios.post('/tasks/linkinvoice', { id, invoiceId, updatedBy: userId })
      .then(() => {
        setFormModal1(false)
        toast.success('Invoice Linked Successfully', {
          position: 'bottom-right'
        })
        getLinkedInvoice(invoiceId)
      })
      .catch((err) => { console.log(err) })
  }

  const loadInvoice = () => {

    axios.post('/taskinvoices/invoicelist', { clientid: selectedTask.clientid, serviceid: selectedTask.serviceid })
      .then((res) => {
        setFormModal1(true)
        setInvoices(res.data.taskinvoices)
      })
      .catch((err) => { console.log(err) })
  }

  useEffect(async () => {
    if (tabId === 'invoice_details') {
      if (selectedTask.invoiceid !== null) {
        getLinkedInvoice(selectedTask.invoiceid)

      }
    }
  }, [tabId])

  return (
    <Card>
      <CardHeader tag='h4'>Invoice Details</CardHeader>
      <Row className='d-flex justify-content-between mt-1'>

        {invoice === null &&
          <Col className='d-flex justify-content-between'>
            <div></div>
            <Button color='primary' size='sm' onClick={() => loadInvoice()} > <Plus size={12} /> Link Invoice</Button>
          </Col>
        }
      </Row>
      <Row>
        {(invoice !== null) &&
          <Table>
            <thead>
              <tr>
                <td>Invoice No</td>
                <td>Invoice Date</td>
                <td>Status</td>
                <td>Amount</td>
              </tr>
            </thead>
            <tbody>
              <tr >
                <td>
                  <Link to={`/${invoice.status === 4 ? 'proposal' : 'invoice'}/view/${invoice.id}`} >
                    <span>{invoice.uniqueno}</span>
                  </Link>
                </td>
                <td>{dateFormatOnly(invoice.invoicedate)}</td>
                <td><Badge color={renderStatus(invoice.paymentstatus).color}> {renderStatus(invoice.paymentstatus).name}</Badge></td>
                <td>{invoice.totalamount}</td>
              </tr>
              {/* <tr>
                <td colSpan={2}></td>
                <td>Profit</td>
                <td>{selectedTask.profit}</td>
              </tr>
              <tr>
                <td colSpan={2}></td>
                <td>Percentage</td>
                <td></td>
              </tr> */}
            </tbody>
          </Table>
        }
      </Row>
      <Modal fullscreen isOpen={formModal1} toggle={() => setFormModal1(!formModal1)} className='modal-dialog-centered'>
        <ModalBody>
          <Row className='d-flex justify-content-between'>
            <h6>Invoice Details</h6>
            <Col className='d-flex justify-content-between'>
              <div></div>
              <div>
                <Button color='warning' size='sm' onClick={() => closeModal1()} > Cancel</Button>
                <Button color='primary' size='sm' className='ms-1' tag={Link} to={'/invoice/add'} > Add Invoice</Button>
                <Button color='success' size='sm' className='ms-1' onClick={() => linkInvoice()} > Link Invoice</Button>
              </div>
            </Col>
          </Row>
          <Table>
            <thead>
              <tr>
                <td>Select</td>
                <td>Invoice No</td>
                <td>Invoice Date</td>
                <td>Customer</td>
                <td>Invoice Items</td>
                <td>Amount</td>
                <td>Balance</td>
                <td>Status</td>
              </tr>
            </thead>
            <tbody>
              {
                invoices.map((obj, k) => {
                  return (
                    <tr key={k} >
                      <td>
                        <Input type='radio' name={`option_invoice`} value={obj.id} onChange={(e) => setInvoiceId(e.target.value)} />
                      </td>
                      <td>
                        {obj.uniqueno}
                      </td>
                      <td>{dateFormatOnly(obj.invoicedate)}</td>
                      <td>
                        <span>{obj.contactname}</span>
                        <p>{obj.contactemail}</p>
                      </td>
                      <td>{obj.taskinvoiceitemsservicename}</td>
                      <td>{obj.totalamount}</td>
                      <td>{obj.dueamount}</td>
                      <td><Badge color={renderStatus(obj.paymentstatus).color}> {renderStatus(obj.paymentstatus).name}</Badge></td>

                    </tr>
                  )
                })
              }
            </tbody>
          </Table>
        </ModalBody>
      </Modal>
    </Card>
  )
}

export default InvoiceDetails
